.sign-in-form {
  // width: 450px;
  margin-top: 30px;
  .inputs-style {
    padding: 20px;
  }
  .checkbox {
    display: flex;
    align-items: center;
    .checkbox-label {
      padding-right: 10px;
    }
  }
  .sign-in-button-style {
    width: 100%;
  }
  .sign-in-buttons {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    flex-flow: wrap;
    .sign-in-buttons-style {
      cursor: pointer;
      color: #3d3d3d;
      &:hover {
        color: #007bff;
      }
    }
  }
}
